* {
  font-family: "Ubuntu Mono", monospace;
  color: #2c2c2c;
}

h1 {
  font-size: 50px;
}

p {
  color: #e3e3e2;
}

span.red {
  color: #d30202;
}

span.green {
  color: #005e00;
}

span.black {
  color: black;
  font-family: Arial, Helvetica, sans-serif;
}

span.orange {
  color: #a35d00;
}

.bg {
  margin: 0;
  background: transparent;
  width: 100%;
  height: 100%;
}

#background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-image: url("blueprint-texture-free-background-thumb33.png");
  background-size: cover;
  background-position: center;
  z-index: -2;
}

#overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(82, 151, 204, 0.858);
  z-index: -1;
}

#sticky-header {
  display: flex;
  justify-content: space-between;
  background: #89cff0f1;
  position: fixed;
  width: 100%;
  height: 60px;
  top: 0;
  background-image: linear-gradient(to bottom, rgba(137, 207, 240, 0.944), transparent);
  -webkit-mask-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.682), transparent);
  mask-image: linear-gradient(to bottom, rgb(255, 255, 255), transparent);

  #mobile-nav-menu,
  #home {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 60px;
    width: 60px;
  }

  i {
    font-size: 35px;
    opacity: .8;
  }
}

#greeting {
  display: flex;
  flex-direction: column;
  margin-top: 60px;
  justify-content: center;
  text-align: center;

  p {
    margin-top: 40px;
  }
}

#empty-div {
  width: 100%;
  height: 400px;
}

#empty-div2 {
  width: 100%;
  height: 200px;
  border-top: 3px double white;
}

#about-me,
#projects,
#contact-me {
  margin: 0 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;

  p {
    text-align: start;
  }
}

#contact-me p {
  text-align: center;
}

#projects img {
  margin: 0 0 20px;
  max-height: 275px;
  max-width: 350px;
  width: auto;
  height: auto;
  max-width: 100%;
  align-self: center;
}

#contact-links {
  margin: 50px 0;
  display: flex;
  justify-content: space-between;

  i {
    font-size: 50px;
  }
}

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.arm {
  animation: wave 2s infinite;
  transform-origin: 100px 90px;
  /* Set the origin point for the arm rotation */
}

@keyframes wave {
  0% {
    transform: rotate(60deg);
  }

  50% {
    transform: rotate(90deg);
  }

  100% {
    transform: rotate(60deg);
  }
}